




























import { Input, Form, FormItem, Button, Alert} from 'element-ui'
import md5 from 'js-md5'
export default {
    name: "Login2",
    data () {
        let ck_username = (rule, value, callback)=> {
            this.form.isOK[0] = false;
            let res = /^[a-zA-z]([a-zA-Z0-9\s]|[.@_]){4,}$/;
            if (value == "" || value == null){
                callback(new Error(""));
                return;
            }
            if (!res.test(value)){
                if (/^([a-zA-z0-9\s]|[.@_]){4,}/.test(value)){
                    callback(new Error(""));
                } else{
                    callback(new Error(""));
                }
            }else{
                this.form.isOK[0] = true;
                callback();
            }
        }
        let ck_password = (rule, value, callback)=>{
            this.form.isOK[1] = false;
            let res = /^([a-zA-Z0-9]|[.@_]){6,}$/;
            if (value == null || value == ""){
                callback(new Error(""));
                return;
            }
            if (!res.test(value)){
                callback(new Error(""));
                return;
            }else{
                if (value.length < 6 ){
                    callback(new Error("6"))
                }else{
                    this.form.isOK[1] = true;
                    callback();
                }
            }

        }
        return {
            contextImage:{
                'background-image': 'url(\''+ this.$serverUrls.images+'context/login-bg.jpg\')',
                'font-size': '18px'
            },
            form: {
                username: '',
                password: '',
                isOK: [false, false]
            },
            form_rule:{
                username: [
                    {validator: ck_username, trigger: 'blur'},
                ],
                password:[
                    {validator: ck_password, trigger: 'blur'}
                ]
            }
        }
    },
    components: {
        [Input.name]: Input,
        [Form.name]: Form,
        [FormItem.name]: FormItem,
        [Button.name]: Button,
        [Alert.name]: Alert
    },
    methods: {
        login: function () {
            if (!this.form.isOK[1] || !this.form.isOK[0]){
                if (!this.form.isOK[0]){
                    this.$refs.username.focus();
                }else{
                    this.$refs.password.focus();
                }
                return;
            }
            let _this = this;
            this.$ajax.send({
                method: 'post',
                url: this.$urls.u_login,
                data: {
                    username: this.form.username,
                    password: md5(this.form.password)
                }
            }).then((ret)=>{
                if (ret.status != 200){
                    return;
                }
                let code = ret.data.code;
                if (code == 200){
                    _this.$cookies.set('token', ret.data.data.token)
                    _this.$cookies.set('user', JSON.stringify(ret.data.data.user))
                    window.location.href="index";
                }else{
                    if (code == 201){
                        this.$message( {
                            showClose:true,
                            message: '',
                            type: 'error'
                        });
                    }
                    if (code == 202){
                        this.$message({
                            showClose:true,
                            message: '',
                            type: 'error'
                        });
                    }
                }
            })
        },
    }
}
